<template>
  <div class="component">
    <div class="container">
      <div class="row">
        <div class="col-md-10 col-sm-12 col-xs-12 offset-md-1 pb-4  pt-4">
          <div class="card">
            <div class="">
              <form @submit.prevent="handleForgetPasswordForm()" v-show="!reset">
                <h5 class="text-white p-2"> Verify Phone Number </h5>
                <div class="form-group">
                  <label for="forgot_phone">Phone Number</label>
                  <input type="text" class="form-control rounded-0"
                        v-model="forgetData.phone"
                        required
                        id="forgot_phone"
                        aria-describedby="phoneHelp" placeholder="Enter Phone Number">
                  <small id="phoneHelp" class="form-text text-muted" style="color: red !important;">
                    {{ responseErrors ? responseErrors.phone[0] : '' }}
                  </small>
                </div>

                <button type="submit" class="btn btn-primary rounded-0"
                        v-if="!isLoading" :disabled="forgetData.phone.length < 10">Verify
                  Phone
                  Number
                </button>
                <LoadingIndicator v-if="isLoading"></LoadingIndicator>
              </form>

              <form @submit.prevent="handleResetPasswordForm()" v-if="reset">
                <div class="form-group">

                  <label for="reset_code">Enter Reset Code</label>
                  <input type="text" class="form-control rounded-0"
                        v-model="resetData.code"
                        required
                        id="reset_code" aria-describedby="phoneHelp" placeholder="Enter Reset Code">
                  <small id="codeHelp" class="form-text text-muted">
                    Please enter your new code
                  </small>
                </div>

                <div class="form-group">
                  <label for="reset_password">Password</label>
                  <input type="password" class="form-control"
                        v-model="resetData.password"
                        required
                        id="reset_password" placeholder="Password">
                </div>
                <div class="form-group">
                  <label for="reset_confirm_password">Confirm Password</label>
                  <input type="password" class="form-control"
                        v-model="resetData.password_confirmation"
                        required
                        id="reset_confirm_password" placeholder="Confirm Password">
                </div>
                <button type="submit" class="btn btn-primary rounded-0"
                        v-if="!isLoading"
                        :disabled="!resetData.code || !resetData.password || resetData.password != resetData.password_confirmation">
                  Reset Password
                </button>
                <LoadingIndicator v-if="isLoading"></LoadingIndicator>
              </form>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import axios from 'axios'
  import {FORGOT_PASSWORD, RESET_PASSWORD_URL} from '../../api_routes'
  import LoadingIndicator from "../Utilities/LoadingIndicator";

  export default {
    name: 'ResetPassword',
      components: {LoadingIndicator},
      computed: {},
    data () {
      return {
        reset: false,
        isLoading: false,
        forgetData: {
          phone: ''
        },
        resetData: {
          code: '',
          password: '',
          password_confirmation: '',
          uuid: ''
        },
        responseErrors: null
      }
    },
    methods: {
      handleForgetPasswordForm () {
        this.isLoading = true;
        axios.post(FORGOT_PASSWORD, this.forgetData).then((response) => {
          this.reset = true;
          this.isLoading = false;
          this.$noty.success('Phone number confirmed, please complete the form to set a new password')
        }, (error) => {
          this.isLoading = false;
          let responseData = error.response.data.errors;
          this.responseErrors = error.response.data.errors;
          let responseMessage = responseData.phone ? responseData.phone[0] : '';
          this.$noty.error(responseMessage)
        })
      },
      handleResetPasswordForm () {
        this.isLoading = true;
        let postData = {
          code: this.resetData.code,
          password: this.resetData.password,
          password_confirmation: this.resetData.password_confirmation,
          phone: this.forgetData.phone

        };
        axios.post(RESET_PASSWORD_URL, postData).then(response => {
          let resetResponseData = response.data;
          if (resetResponseData) {
            this.isLoading = false;
            this.$noty.success('Password was changed successfully');
            this.$router.push({name: 'login'})
          }
        }, (error) => {
            this.isLoading = false;
          let responseData = error.response.data.errors;
          let errorMessage = responseData.password ? responseData.password[0] : responseData.code[0];
          if (responseData) {
            this.$noty.error(errorMessage)
          } else {
            this.$noty.error('We have problems processing your request, Please try again later')
          }
        })
      }
    }
  }
</script>

<style scoped lang="stylus">
  .card-body
    background #0d0f1c
</style>
